.products {
  margin-top: 95px;
  &__wrap {
    align-items: center;
    padding: 0;
  }
  &__title {
    margin-bottom: 63px;
    font-family: "DINPro";
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 2px;
    color: $txt-color;
    text-transform: uppercase;
    text-align: center;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    position: relative;
    max-width: 542px;
    height: 305px;
    margin-bottom: 14px;
  }
  &__item-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, #919191 0%, #000000 100%);
    opacity: 0.5;
  }
  &__item-title {
    position: absolute;
    top: 47px;
    left: 48px;
    max-width: 440px;
    color: #ffffff;
    font-family: "DINPro";
    font-size: 24px;
    font-weight: 500;
    letter-spacing: 2.4px;
    text-transform: uppercase;
  }
  &__item-text {
    position: absolute;
    top: 109px;
    left: 48px;
    color: #ffffff;
    font-weight: 400;
    line-height: 24px;
  }
  &__item-btn-group {
    position: absolute;
    top: 215px;
    left: 48px;
  }
  &__item-btn {
    width: 144px;
    height: 45px;
    margin-right: 16px;
    border-radius: 25px;
    border: 1px solid #d3d3d3;
    background-color: #ffffff;
    font-weight: 400;
    letter-spacing: 0.8px;
    color: $btn-color;
    transition: 0.3s;
    &:hover {
      background-color: $btn-color;
      color: #ffffff;
      border-color: $btn-color;
    }
  }
}